<template>
  <div
    class="auth-holder"
    v-loading.fullscreen.lock="loading"
    element-loading-text="Analyzing your lip movement..."
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="text-container">
      <p>
        Please ensure that your face is within the frame and read out the
        following sentence
      </p>
      <h3 :style="read_text_color">{{ text }}</h3>
    </div>
    <div class="video-display-container" @click="toggle_mode">
      <div class="webcam-border" :style="video_border">
        <VideoCapture />
      </div>
    </div>
    <div class="recorder-btn-holder">
      <el-button
        :disabled="record_disable"
        :type="recorder_style"
        round
        @click="toggleBtnColor"
      >
        <font-awesome-icon icon="microphone" />
      </el-button>
    </div>
  </div>
</template>

<script>
import { VideoCapture } from "vue-media-recorder";
import router from "@/router";
export default {
  data() {
    return {
      mode: "success",
      text: "The quick brown fox jumps over the lazy dog.",
      loading: false,
      counter: 0,
      recording: false,
      record_disable: false,
      recorder_style: "primary",
      video_border: {
        "border-color": "#409EFF"
      },
      read_text_color: {
        color: "#409EFF"
      },
      show_next: false
    };
  },
  components: {
    VideoCapture
  },
  methods: {
    toggle_mode() {
      if (this.mode == "success") this.mode = "fail";
      else if (this.mode == "fail") this.mode = "success";
    },
    toggleBtnColor() {
      if (!this.recording) {
        this.recorder_style = "danger";
        this.video_border = {
          "border-color": "#F56C6C"
        };
        this.read_text_color = {
          color: "#F56C6C"
        };
        this.recording = true;
      } else {
        this.recorder_style = "primary";
        this.video_border = {
          "border-color": "#409EFF"
        };
        this.read_text_color = {
          color: "#409EFF"
        };
        this.recording = false;
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          if (this.mode === "success") router.push("/success/success");
          else if (this.mode === "fail") router.push("/fail");
        }, 2000);
      }
    }
  }
};
</script>

<style>
.text-container {
  padding: 20px;
  font-size: 12px;
  text-align: center;
}
</style>
