<template>
  <div
    class="home"
    v-loading.fullscreen.lock="loading"
    element-loading-text="Analyzing your lip movement..."
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="title-text">
      <p style="color: #303133">
        Please ensure that your face is within the frame and read out the
        following sentence
      </p>
      <h3 :style="read_text_color">{{ text }}</h3>
    </div>
    <div class="video-display-container">
      <div class="webcam-border" :style="video_border">
        <VideoCapture />
      </div>
    </div>
    <div class="recorder-btn-holder">
      <el-button
        :disabled="record_disable"
        :type="recorder_style"
        round
        @click="toggleBtnColor"
      >
        <font-awesome-icon icon="microphone" />
      </el-button>
    </div>
    <div class="record-results">
      <transition name="fade">
        <div v-show="show_first_result" class="result-alert">
          <el-alert
            show-icon
            type="success"
            :closable="false"
            title="Your 1st attempt is analyzed!"
          ></el-alert>
        </div>
      </transition>
      <transition name="fade">
        <div v-show="show_second_result" class="result-alert">
          <el-alert
            show-icon
            type="success"
            :closable="false"
            title="Your 2nd attempt is analyzed!"
          ></el-alert>
        </div>
      </transition>
      <transition name="fade">
        <div v-show="show_third_result" class="result-alert">
          <el-alert
            show-icon
            type="success"
            :closable="false"
            title="Your 3rd attempt is analyzed!"
          ></el-alert>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="proceed-container" v-show="show_next">
        <el-button type="primary" round @click="go_next">
          <font-awesome-icon icon="play" />
        </el-button>
      </div>
    </transition>
  </div>
</template>

<script>
import { VideoCapture } from "vue-media-recorder";
import router from "@/router";
export default {
  name: "Home",
  data() {
    return {
      loading: false,
      text: "The quick brown fox jumps over the lazy dog.",
      counter: 0,
      recording: false,
      record_disable: false,
      show_first_result: false,
      show_second_result: false,
      show_third_result: false,
      recorder_style: "primary",
      video_border: {
        "border-color": "#409EFF"
      },
      show_next: false,
      read_text_color: {
        color: "#409EFF"
      }
    };
  },
  components: {
    VideoCapture
  },
  methods: {
    toggleBtnColor() {
      if (!this.recording) {
        this.recorder_style = "danger";
        this.video_border = {
          "border-color": "#F56C6C"
        };
        this.read_text_color = {
          color: "#F56C6C"
        };
        this.recording = true;
      } else {
        this.recorder_style = "primary";
        this.video_border = {
          "border-color": "#409EFF"
        };
        this.read_text_color = {
          color: "#409EFF"
        };
        this.recording = false;

        if (this.counter == 0) {
          this.counter++;
          this.loading = true;
          setTimeout(() => {
            this.show_first_result = true;
            this.loading = false;
          }, 1500);
        } else if (this.counter == 1) {
          this.counter++;
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.show_second_result = true;
          }, 1500);
        } else if (this.counter == 2) {
          this.counter++;
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.show_third_result = true;
            this.record_disable = true;
            this.show_next = true;
          }, 1500);
        }
      }
    },
    go_next() {
      router.push("/success/all-set");
    }
  }
};
</script>

